import { Component, OnInit } from '@angular/core';
import {FormioAuthComponent, FormioAuthService} from 'angular-formio/auth';
import { Formio } from 'angular-formio';
import { AppConfig } from '../../config';

@Component({
  selector: 'app-auth',
  templateUrl: './auth.component.html',
  styleUrls: ['./auth.component.scss']
})
export class AuthComponent extends FormioAuthComponent implements OnInit {
  public showLogin = true;
  constructor(
    public config: AppConfig,
    public auth: FormioAuthService
  ) {
    super();
  }

  ngOnInit() {
    if ((window as any).APP_SSO || this.config.query.sso || this.config.query.saml) {
      // Trigger an SSO login.
      this.showLogin = false;
      Formio.authUrl = (window as any).SSO_PROJECT;
      Formio.ssoInit((window as any).APP_SSO || this.config.query.sso || 'saml', {
        forceAuth: true
      });
    }
    if (!this.auth.authenticated) {
      localStorage.removeItem(`${Formio.namespace}Token`);
      localStorage.removeItem(`${Formio.namespace}User`);
      localStorage.removeItem(`${Formio.namespace}AppUser`);
    }
  }
}
